<template>
    <div class="transfer">
        <MyMenu />
        <a-row class="t-left">

            <a-col :span="this.g.container.span" :offset="this.g.container.offset">
                <a-row>
                    <a-col class="bg-white" style="padding:15px;">
                        <a-row class="mtb30">
                            <a-col>
                                Please select currency：
                                <a-radio-group v-model="walletListDefaultIndex" @change="onChangeRadio" :default-value="walletListDefaultIndex">
                                    <a-radio-button v-for="item in walletList" :key="item.id" :value="item.name" :disabled="item.name!='usdt'">
                                        {{item.name.toLocaleUpperCase()}}
                                    </a-radio-button>
                                </a-radio-group>
                            </a-col>
                        </a-row>
                        <a-row class="mtb30" type="flex" align="middle">
                            <a-col flex="100px">Transfer accounts：</a-col>
                            <a-col flex="100px">My assets： {{my_assets.real}}</a-col>
                            <a-col flex="auto"></a-col>
                            <a-col flex="200px">Contract account： {{my_contract.real}}</a-col>
                        </a-row>
                        <a-row class="mtb30" type="flex" align="middle">
                            <a-col flex="100px">Drag the transfer：</a-col>
                            <a-col flex="100px">
                                <a-input-number @keyup="onChangeSlider" v-model="my_assets.value" :min="0" :max="my_assets.value+my_contract.value" style="marginLeft: 16px" />
                            </a-col>
                            <a-col flex="auto">
                                <a-slider @change="onChangeSlider" @afterChange="onChangeSlider" v-model="my_assets.value" :min="0" :max="my_assets.value+my_contract.value" />
                            </a-col>
                            <a-col flex="100px">
                                <a-input-number @keyup="onChangeSlider" readonly="readonly" v-model="my_contract.value" :min="0" :max="my_assets.value+my_contract.value" style="marginLeft: 16px" />
                            </a-col>
                        </a-row>
                        <a-row class="mtb30" type="flex" justify="center">

                            <a-popconfirm :disabled="my_assets.value==my_assets.real" title="Are you sure?" ok-text="Yes" cancel-text="No" @confirm="confirm">
                                <a-button :disabled="my_assets.value==my_assets.real" type="primary" block>
                                    Submit a definitive transfer
                                </a-button>
                            </a-popconfirm>
                        </a-row>
                    </a-col>
                </a-row>

                <div class="mtb30"></div>

                <h4>Transfer record</h4>
                <a-table :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id" class="bg-white">
                    <template slot="amount" slot-scope="text, record">
                        <div v-if="record.action == 'add'">
                            <span class="t-green">+</span> {{text}}
                        </div>
                        <div v-if="record.action == 'sub'">
                            <span class="t-red">-</span> {{text}}
                        </div>
                    </template>
                </a-table>

            </a-col>
        </a-row>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Transfer",
    components: {
        MyMenu,
    },
    data() {
        return {
            loading: false,

            walletList: [],
            walletListDefaultIndex: "usdt",

            my_assets: {
                real: 0,
                value: 0,
            },
            my_contract: {
                real: 0,
                value: 0,
            },
            total: 0,


            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 5,
            },
            queryParam: {
                page: 1,
                size: 5,
                user_id: 0,
            },
            columns: [
                {
                    title: "Currency",
                    dataIndex: "coin_name",
                    width: "20%",
                },
                {
                    title: "Amount",
                    dataIndex: "amount",
                    scopedSlots: { customRender: 'amount' },
                },
                {
                    title: "Balance",
                    dataIndex: "balance",
                },
                {
                    title: "Time",
                    dataIndex: "create_datetime",
                    width: "20%",
                },
            ],
        };
    },
    methods: {
        confirm() {
            this.onClickSubmitTransfer()
        },
        onClickSubmitTransfer() {
            const body = {
                user_id: this.g.userInfo.id,
                coin_type: this.walletListDefaultIndex,
                assets_amount: this.my_assets.value,
                contract_amount: this.my_contract.value,
            }
            console.log("onClickSubmitTransfer", body)

            $.post(`${this.g.apiUrl}/api/my/transfer`, body, (res) => {
                if (res.flag != "ok") {
                    this.$message.error(res.flag);
                    return
                }
                this.$message.success('Succeed!');
                this.init()
            }).fail((err) => {
                this.$message.error(err.responseText);
            })
        },
        onChangeRadio() {
            console.log(this.walletListDefaultIndex)
            this.updateValue()
        },
        onChangeSlider() {
            this.my_contract.value = this.total - this.my_assets.value;
        },
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            this.getTableList();
        },
        formatDate(list) {
            let a = list;
            a.forEach(function (item) {
                item.create_datetime = moment(item.create_datetime).format("YYYY-MM-DD HH:mm:ss");
            });
            return a;
        },
        getTableList() {
            this.loading = true;
            $.get(`${this.g.apiUrl}/api/my/transfer/history`, this.queryParam, (res) => {
                this.loading = false;
                if (res.flag != "ok") {
                    this.$message.error(res.flag);
                    return;
                }
                const pagination = { ...this.pagination };
                pagination.total = res.data.page.total;
                this.tableDatas = this.formatDate(res.data.list);
                this.pagination = pagination;
            }
            );
        },
        updateValue() {
            const currCoin = this.getWallet(this.walletList, this.walletListDefaultIndex)
            this.my_assets.value = currCoin.assets_amount
            this.my_assets.real = currCoin.assets_amount
            this.my_contract.value = currCoin.contract_amount
            this.my_contract.real = currCoin.contract_amount
            this.total = this.my_contract.value + this.my_assets.value
        },
        getWallet(walletList, coinName) {
            for (let i = 0; i < walletList.length; i++) {
                if (walletList[i].name == coinName) {
                    return walletList[i]
                }
            }
            return null
        },
        initWalletList() {
            const query = { user_id: this.g.userInfo.id }
            $.get(`${this.g.apiUrl}/api/my/assets`, query, (res) => {
                if (res.flag != 'ok') {
                    this.$message.error(res.flag)
                    return
                }
                this.walletList = res.data.walletList
                this.updateValue()
            })
        },
        init() {
            this.queryParam.user_id = this.g.userInfo.id
            this.initWalletList()
            this.getTableList()
        },
    },
    mounted() {
        this.init();
    },
};
</script>



